<template>
  <b-container class="w-100 justify-content-md-center">
      <b-row class="w-100">
        <b-col>
        </b-col>
        <b-col cols="6" style="max-height:500px">
          <b-form v-if="connected === false" inline>
            <label class="sr-only" for="inline-form-input-username">Username</label>
            <b-input-group prepend="@" class="mb-2 mr-sm-2 mb-sm-0">
              <b-input id="inline-form-input-username" placeholder="Username" v-model="user"></b-input>
            </b-input-group>

            <b-button variant="primary" @click.prevent="connect">Connect</b-button>
          </b-form>
          <b-list-group class="overflow-auto pre-scrollable"  v-if="connected === true" style="font-color:white">
            <b-list-group-item style="background-color: #ccccb3" @click.prevent="send_chat('HISTORY')">
              load previous
            </b-list-group-item>
            <b-list-group-item v-for="item in received_messages" :key="item.id" :style="checkStyle(item.user)">
            <span v-if="item.user === 'sys'">{{ item.content }}</span>
            <span v-else-if="item.user === user">{{ item.content }} :::{{ item.user }}:</span>
            <span  v-else>{{ item.user }}::: {{ item.content }}</span>
            </b-list-group-item>
          </b-list-group>
        </b-col>
        <b-col>
          <b-list-group v-if="connected === true">
            <b-list-group-item variant="success">
              <h3>{{ user }}</h3>
            </b-list-group-item>
            <b-list-group-item variant="light">
              <b-form v-if="change_status === true" inline>
                <label class="sr-only" for="inline-form-input-username">Status</label>
                <b-input-group prepend="#" class="mb-2 mr-sm-2 mb-sm-0">
                  <b-input id="inline-form-input-username"  v-model="status"></b-input>
                </b-input-group>

                <b-button variant="success" @click="change_status=false;send_chat('STATUS')">Send</b-button>
              </b-form>
              <h5 v-else @click="change_status=true">Status: {{ status }}</h5>
            </b-list-group-item>
            <b-list-group-item>
              <b-button variant="danger" @click="leave_disconnect">Disconnect</b-button>
            </b-list-group-item>
            </b-list-group>
        </b-col>
    </b-row>
    <b-row class="w-100 justify-content-md-center">
      <b-col>
      </b-col>
      <b-col cols="12">
      <b-list-group v-if="connected === true">
        <b-list-group-item>
      <b-form>
        <label class="sr-only" for="inline-form-input-username">Your message...</label>
        <b-input-group prepend=">" class="mb-2 mr-sm-2 mb-sm-0">
          <b-input id="inline-form-input-username" placeholder="Your message..." v-model="send_message"></b-input>
        </b-input-group>

        <b-button variant="success" @click.prevent="send_chat('CHAT')">Send</b-button>
      </b-form>
        </b-list-group-item>
      </b-list-group>
    </b-col>
    <b-col>
    </b-col>
    </b-row>
  </b-container>
</template>

<script>
import SockJS from 'sockjs-client'
import Stomp from 'webstomp-client'

export default {
  name: 'HelloWorld',
  data () {
    return {
      received_messages: [],
      system_messages: [],
      users: [],
      send_message: null,
      connected: false,
      change_status: false,
      user: null,
      status: 'Connected!'
    }
  },
  methods: {
    send_connect () {
      console.log('Send message:' + this.user)
      if (this.stompClient && this.stompClient.connected) {
        const msg = { user: this.user,
          content: '',
          type: 'JOIN',
          id: ''}
        console.log(JSON.stringify(msg))
        this.stompClient.send('/app/system', JSON.stringify(msg), {})
      }
    },
    send_chat (type) {
      // console.log(JSON.stringify(msg))
      if (type === 'CHAT') {
        let msg = {user: this.user, content: this.send_message, type: type, id: ''}
        this.stompClient.send('/app/chat', JSON.stringify(msg), {})
      } else if (type === 'HISTORY') {
        var id = this.received_messages[1].id
        if (this.received_messages[0].id !== '') {
          id = this.received_messages[0].id
        }
        let msg = {user: this.user, content: id, type: type, id: ''}
        this.stompClient.send('/app/system', JSON.stringify(msg), {})
      } else if (type === 'STATUS') {
        let msg = {user: this.user, content: this.status, type: type, id: ''}
        // this.change_status = false
        this.stompClient.send('/app/system', JSON.stringify(msg), {})
      } else if (type === 'LEAVE') {
        let msg = {user: this.user, content: '', type: type, id: ''}
        this.stompClient.send('/app/system', JSON.stringify(msg), {})
      }

      this.send_message = ''
    },
    onChatMessageReceived (payload) {
      const message = JSON.parse(payload.body)
      console.log(payload)
      let chatMessage = {user: message.user, content: '', type: message.type, id: ''}
      if (message.type === 'JOIN') {
        chatMessage.content = 'Greeting, everyone!'
      } else if (message.type === 'LEAVE') {
        chatMessage.content = 'Bye, bye!'
      } else {
        chatMessage.content = message.content
        chatMessage.id = message.id
      }
      this.received_messages.push(chatMessage)
    },
    onSysMessageReceived (payload) {
      const message = JSON.parse(payload.body)
      console.log(message)
      if (message.type === 'HISTORY') {
        var historyArr = JSON.parse(message.content)
        console.log(historyArr)
        for (var i = 0; i < historyArr.length; i++) {
          this.received_messages.unshift(historyArr[i])
        }
      } else if (message.type === 'ERROR') {
        console.log(message.content)
        if (message.content === 'User already in chat! Try other nickname.') {
          this.disconnect()
        }
      } else if (message.type === 'ROOM') {
        console.log(message.content)
      } else if (message.type === 'SERVER') {
        console.log(message.content)
      }
    },
    connect () {
      this.socket = new SockJS('http://localhost:8081/gs-guide-websocket')
      this.stompClient = Stomp.over(this.socket)
      this.stompClient.connect(
        {},
        frame => {
          this.connected = true
          console.log(frame)
          const urlArr = this.socket._transport.url.split('/')
          const index = urlArr.length - 2
          const sessionId = urlArr[index]
          this.stompClient.subscribe('/topic/public', this.onChatMessageReceived)
          this.stompClient.subscribe('/topic/system-user' + sessionId, this.onSysMessageReceived)
          this.stompClient.subscribe('/topic/system', this.onSysMessageReceived)
          this.send_connect()
          this.status = 'Connected!'
        },
        error => {
          console.log(error)
          this.connected = false
        }
      )
    },
    disconnect () {
      if (this.stompClient) {
        this.stompClient.disconnect()
      }
      this.connected = false
    },
    leave_disconnect () {
      this.send_chat('LEAVE')
      this.disconnect()
    },
    tickleConnection () {
      this.connected ? this.disconnect() : this.connect()
    },
    scrollToEnd () {
      var container = document.querySelector('.pre-scrollable')
      var scrollHeight = container.scrollHeight
      container.scrollTop = scrollHeight
    },
    checkStyle (user) {
      var style
      switch (user) {
        case 'sys':
          style = 'background-color: #ff0066;  border: 1px solid grey; text-align:center; color: white'
          break
        case this.user:
          style = 'background-color: #00cc66;  border: 1px solid grey; text-align:right; color: white'
          break
        default:
          style = 'background-color: #0099ff;  border: 1px solid grey; text-align:left; color: white'
          break
      }
      return style
    }
  },
  mounted () {
    this.scrollToEnd()
  },
  updated () {
    this.scrollToEnd()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



